<template>
  <transition name="none">
    <div v-if="show" id="splashScreen" class="splash-screen d-flex justify-content-center align-items-center"
      ref="splashScreen">
      <div class="d-flex flex-column align-items-center">
        <img class="logo mt-5" src="../../public/WeekToDo-Logo-Color.svg" alt="WeekToDo Logo">
        <h1>WeekToDo</h1>

       
     
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: "splashScreen",
  data() {
    return {
      show: true,
      sponsor: null
    }
  },
  mounted() {
    const axios = require('axios').default;
    axios.get('https://weektodo.me/api/sponsors')
      .then(response => (this.renderSponsor(response)))
      .catch(error => console.log(error.message))
  }
  ,
  methods: {
    hideSplash: function () {
      this.show = false;
    }
    ,
    renderSponsor: function (response) {
      var sponsors = [];

      sponsors.push({
          name: "WeekToDo",
          message: this.$t('donate.splashMessage'),
          url: "https://weektodo.me/support-us",
          img: "/icons/ko-fi.png",
        });

      response.data.bronze.forEach(function (obj) {
        sponsors.push(obj);
      });

      response.data.silver.forEach(function (obj) {
        for (let i = 0; i < 3; i++) {
          sponsors.push(obj);
        }
      });

      response.data.golden.forEach(function (obj) {
        for (let i = 0; i < 7; i++) {
          sponsors.push(obj);
        }
      });

      response.data.diamond.forEach(function (obj) {
        for (let i = 0; i < 15; i++) {
          sponsors.push(obj);
        }
      });

      this.sponsor = sponsors[Math.floor(Math.random() * sponsors.length)];
    }
  }
}
</script>

